import clsx from 'clsx'
import Linkify from 'linkify-react'

import classes from './TextMessage.module.scss'

type TextMessageProps = {
  className?: string
  text: string
  linkProtocol: string
  linkTarget: string
}

const TextMessage = (props: TextMessageProps): JSX.Element => {
  const { className, text, linkProtocol, linkTarget } = props
  return (
    <Linkify
      as='span'
      className={clsx(classes.text, className)}
      options={{
        defaultProtocol: linkProtocol || undefined,
        target: linkTarget || undefined,
        rel: 'noopener noreferrer'
      }}
    >
      {text}
    </Linkify>
  )
}
export default TextMessage
